// @flow

import styled from 'styled-components/macro';
import {page, spacing, colors} from '../config/styles';
import importAll from 'import-all.macro';
import {hostname, employer} from '../config';
import {postMeta} from '../utilities';
import Layout from '../components/application';
import Card, {Cards} from '../components/blog/card';
import {Portrait} from '../assets/img';
import Head from 'next/head';

const posts = Object.entries(importAll.sync('./blog/*.mdx'))
    .reduce(
        (arr, [path, exports]: [string, any]) =>
            arr.concat([postMeta({...exports.meta, path})]),
        []
    )
    .sort((p1, p2) => p2.date - p1.date);

function Banner() {
    return (
        <StyledBanner>
            <BannerImage>
                <Portrait />
            </BannerImage>
            <BannerContent>
                <h1>Welcome!</h1>
                <p>
                    My name is Sidney Liebrand. I am a Dutch front-end developer working
                    at {employer.name} in {employer.city}.
                </p>
            </BannerContent>
        </StyledBanner>
    );
}

const BannerImage = styled.div`
    position: relative;
    flex: 1 0 280px;
    margin: auto;
    min-width: 280px;
    margin: ${spacing.medium};
    margin-left: ${spacing.large};

    &::after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        border: 3px solid ${colors.primary};
        border-radius: 100%;
        // background-color: #09c;
    }

    > * {
        margin: 0;
        border-radius: 100%;
        overflow: hidden;
    }
`;

const BannerContent = styled.div`
    margin: auto ${spacing.large};
    line-height: 1.2;

    > *:first-child {
        margin-top: 0;
    }
`;

const StyledBanner = styled.section`
    position: relative;
    margin: 0 auto;
    display: flex;
    padding: ${spacing.large} ${spacing.medium};
    width: 100%;
    ${page.withVar('width')};
`;

export default function Page() {
    return (
        <Layout>
            <Head>
                <title>{hostname} &nbsp;&bull;&nbsp; homepage</title>
                <meta name="author" content="Sidney Liebrand" />
                <meta
                    name="description"
                    content="Home of Sidney Liebrand, a front-end developer with a sharp eye for detail. Find blog posts and tools written by Sidney right here."
                />
            </Head>
            <Banner />
            <Cards>
                {posts.map(post => (
                    <Card key={post.title} {...post} />
                ))}
            </Cards>
        </Layout>
    );
}
